@import url(https://fonts.googleapis.com/css2?family=Muli:wght@300;400;700;800&display=swap);
*{padding:0;margin:0;outline:0;font-family:'Muli', sans-serif}html,body{height:100vh}#root main{height:100vh}

main{display:grid;grid-template-columns:1fr 1fr}@media (max-width: 768px){main{grid-template-columns:1fr}}main #slider{background-color:#A9C5BA;height:100%}@media (max-width: 768px){main #slider{padding-bottom:1rem}}main #slider .slide img{width:65%;margin:4rem auto 2rem auto;display:flex}@media (max-width: 768px){main #slider .slide img{width:60%;margin:2rem auto 2rem auto}}main #slider .slide div{text-align:center;padding:0rem 6rem;color:#fff}main #slider .slide div h4{font-size:1.5rem;font-weight:200;margin-bottom:.8rem}main #slider .slide div p{font-weight:200}main #formInputs .title{display:flex;justify-content:flex-end;margin:3rem 6rem;font-family:'Avenir Black', sans-serif;font-size:1.5rem}.carousel-dots li{margin:0 .1rem}.carousel-dots-active{background-color:#fff;border-radius:2rem;width:1.3rem !important;height:.6rem !important;vertical-align:sub}.carousel-dots-active button::before,.carousel-dots-active button:hover:before,.carousel-dots-active button:focus:before{opacity:0 !important}.container{padding:0 8rem 2rem}@media (max-width: 830px){.container{padding:0 2rem 2rem}}.container h4{text-align:center;color:#707070;font-size:2rem;font-weight:300;margin:3rem 0}.container .input{margin:.5rem 0}.container .submit{display:flex;justify-content:center;margin:1.8rem 0}.container .submit button{background-color:#707070;border:0;border-radius:2rem;padding:0.7rem 3rem;color:#fff}.container .submit button:hover{cursor:pointer}.container .forgot{font-size:.8rem;color:#707070;margin:1.5rem 0;text-decoration:none;display:flex;justify-content:center}.container .division{display:flex;justify-content:center;align-items:center;margin:1rem 2rem;opacity:.5}.container .division span{width:10rem;height:1px;background-color:#707070}.container .division div{margin:0 1rem}.container .singIn-google{display:flex;justify-content:center;margin:2rem 0}.container .singIn-google button{background-color:#fff;border:0;border-radius:2rem;padding:0.7rem 1.5rem;font-weight:600;font-size:.8rem;box-shadow:0px 3px 6px #00000029;display:flex;align-items:center}.container .singIn-google button:hover{cursor:pointer}.container .singIn-google button img{margin-right:.7rem;width:13%}.container .new-account{text-align:center;color:#707070;margin:2rem 0}.container .new-account .sing-up{color:#A9C5BA;-webkit-text-decoration-line:underline;text-decoration-line:underline;-webkit-text-decoration-color:#A9C5BA;text-decoration-color:#A9C5BA}

